                  label: 'inquiry',
                  text: props.item.text
                  })"
              >
                Inquiry
              </v-btn>
            </td>
            <td style="padding-left: 0px; padding-right: 5px;">
              <v-btn small outline block color="error" 
                @click="labelTweetAs({
                  origin: 'problem_report', 
                  index: props.index, 
                  status_id: props.item.status_id, 
