<script>
  import axios from 'axios';
  import moment from 'moment';
  import 'moment/locale/de'
  import { POST_TWEET_LABEL_ENDPOINT, POST_TWEET_LABEL_PAYLOAD, POST_TWEET_LABEL_BACKUP_ENDPOINT, POST_TWEET_LABEL_BACKUP_PAYLOAD } from'./../../RESTconf.js'
  import {ACTION_SET_TOOLBAR_HEADER, ACTION_REMOVE_UNLABELED_TWEET} from './../../store/types.js'
  export default {
    data() {
      return {
        tableHeaders: [
          {text: 'Date', align: 'center', sortable: true, value: 'created_at', width: '10%'},
          {text: 'Tweet', align: 'left', sortable: false, value: 'tweet', width: '75%'},
          {text: '', align: 'center', sortable: false, value: 'actionProblemReport', width: '5%'},
          {text: 'Actions', align: 'center', sortable: false, value: 'actionInquiry', width: '5%'},
          {text: '', align: 'center', sortable: false, value: 'actionIrrelevant', width: '5%'},
        ],
        randomizedOnce: false,
        erros: [],
        problemReports: [],
        inquiries: [],
        irrelevants: []
      }
    },
    methods: {
      separateTweets(tweets) {
        // randomize tweets, but only once.
        if(!this.randomizedOnce) {
          tweets = tweets.sort(() => Math.random() - 0.5);
          this.randomizedOnce = true;
        }
        
        this.problemReports = []
        this.inquiries = []
        this.irrelevants = []

        tweets.forEach(tweet => {
          if (tweet.tweet_class === 'problem_report') {
            this.problemReports.push(tweet)
          } else if (tweet.tweet_class === 'inquiry') {
            this.inquiries.push(tweet)
          } else if (tweet.tweet_class === 'irrelevant') {
            this.irrelevants.push(tweet)
          }
        });
        
        // Update UI
        this.problemReports.splice(0, 0);
        this.inquiries.splice(0, 0);
        this.irrelevants.splice(0, 0);
      },
      getFormattedDate(date) {
        return moment(date, 'YYYYMMDD').format('DD.MM.YYYY')
      },
      labelTweetAs(labelObject) {
        axios.post(POST_TWEET_LABEL_ENDPOINT(), POST_TWEET_LABEL_PAYLOAD(labelObject.status_id, labelObject.label))
          .then(response => {
            this.removeTweetFromList(labelObject.origin, labelObject.index, labelObject.status_id);
          })
          .catch(e => {
            console.log(e);
            this.errors.push(e);
          });
        
        // send the label to the backupserver, too.
        axios.post(POST_TWEET_LABEL_BACKUP_ENDPOINT(), POST_TWEET_LABEL_BACKUP_PAYLOAD(labelObject.status_id, labelObject.label, labelObject.text))
          .then(response => {
          })
          .catch(e => {
            console.log(e);
            this.errors.push(e);
          });
    },
    removeTweetFromList(origin, index, statusID) {
      if (origin === "problem_report") {
        this.problemReports.splice(index, 1);
      } else if (origin === "inquiry") {
        this.inquiries.splice(index, 1);
      } else if (origin === "irrelevant") {
        this.irrelevants.splice(index, 1);
      }
     
     this.$store.dispatch(ACTION_REMOVE_UNLABELED_TWEET, statusID);
    }
  },
  mounted() {
    this.$store.dispatch(ACTION_SET_TOOLBAR_HEADER, 'Tweet Labeling Task');
  },
  computed: {
    dataUpToDate() {
      if(this.$store.state.dataUpToDate) {
        this.separateTweets(this.$store.state.unlabeledTweets);
      }
      return this.$store.state.dataUpToDate;
    }
  }
}
</script>

