<template>
  <v-card class="echarts">
    <ECharts
      class="chart"
      :options="line"
      :loading="!dataUpToDate"
      auto-resize
    />
  </v-card>
</template>
<script>
  import ECharts from 'vue-echarts';
  import 'echarts';
  import moment from 'moment';
  import 'moment/locale/de'
  import {ITEM_STYLE_LINE_BLUE} from '../../colors.js';

  moment.locale('en');
  export default {
    name: 'ClassFrequencyDistribution',
    components: {
      ECharts
    },
    props: {},
    data: () => ({
      twitterAccount: 'Tre_It',
      loading: false,
      line: {
        title: {
          text: 'Sentiment Weekly Performance Comparision',
          left: 'center',
          right: 'center'
        },
        tooltip: {
          trigger: 'axis'
        },
        grid: {
          top: '20%',
          bottom: '10%',
          left: '5%',
          right: '5%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: ['Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa', 'Su']
        },
        yAxis: {
          name: 'Average Sentiment',
          min: -5,
          max: 5,
          interval: 1,
          type: 'value',
          nameLocation: 'center',
          nameTextStyle: {
            padding: 10,
            fontSize: 10
          }
        },
        series: [
          {
            name: 'last week',
            type: 'line',
            symbolSize: 5,
            smooth: true,
            itemStyle: ITEM_STYLE_LINE_BLUE,
            lineStyle: ITEM_STYLE_LINE_BLUE,
            data: []
          }
        ]
      }
    }),
    methods: {
      /**
       * Calculates the days of the week from today backwards.
       * @returns {*[]} daysOfTheWeek A list of abbreviated days of the week ending with today.
       */
      fromToday() {
        let daysOfTheWeek = [];
        for (let i = 0; i < 7; i++) {
          daysOfTheWeek.push(moment(-i).subtract(i, 'days').format('ddd'));
        }
        return daysOfTheWeek.reverse();
      },
      loadChartData(tweets) {
        let uniqueDates = new Set();
        let minDate = 0
        let maxDate = 0

        let dictTweetSentimentPerDay = {};
        tweets.forEach(function (tweet) {
          let date = tweet.created_at;
          uniqueDates.add(date);
          if (minDate == 0 || minDate > date) {
              minDate = date;
          } else if (maxDate < date) {
            maxDate = date;
          }
          dictTweetSentimentPerDay[date] = {
              count: date in dictTweetSentimentPerDay ? ++dictTweetSentimentPerDay[date].count : 0,
              senitmentSum: date in dictTweetSentimentPerDay ? dictTweetSentimentPerDay[date].senitmentSum + tweet.sentiment_score : tweet.sentiment_score,
              sentimentAvg: date in dictTweetSentimentPerDay ? dictTweetSentimentPerDay[date].senitmentSum / dictTweetSentimentPerDay[date].count : 0
            }
        });

        // dynamically create the xAxis labels
        let xAxisLabels = []
        uniqueDates.forEach(date => {
            xAxisLabels.push(moment(date, 'YYYYMMDD').format('DD.MM'))
        });
        this.line.xAxis.data = xAxisLabels;

        // create the chart data
        let sentimentAvgPerDay = []
        Object.keys(dictTweetSentimentPerDay).forEach(function (date) {
          sentimentAvgPerDay.push(dictTweetSentimentPerDay[date].sentimentAvg)
        });
        this.line.series[0].data = sentimentAvgPerDay;
      },
      getFormattedDateAsInt(count) {
        let date = new Date();
        date.setDate(date.getDate() - -count);

        let year = date.getFullYear();
        let month = `${date.getMonth() + 1}`.padStart(2, 0);
        let day = `${date.getDate()}`.padStart(2, 0);
        return parseInt(year + month + day)
      }
    },
    computed: {
      dataUpToDate() {
        if(this.$store.state.dataUpToDate) {
          this.loadChartData(this.$store.state.filteredTweets)
        }
        return this.$store.state.dataUpToDate;
      }
    }
  }
</script>
<style lang="scss" scoped>
.echarts {
  min-height: 250px;
  height: 100%;
  width: 100%;
  .chart {
    width: 100%;
    height: 100%;
  }
}
</style>
