<template>
  <v-card class="echarts">
    <ECharts
      class="chart"
      :options="bar"
      :loading="!dataUpToDate"
      auto-resize
    />
  </v-card>
</template>

<script>
  import ECharts from 'vue-echarts';
  import 'echarts';
  import {ITEM_STYLE_BAR_BLUE} from '../../colors.js';

  export default {
    name: 'ClassFrequencyDistribution',
    components: {
      ECharts
    },
    props: {},
    data: () => ({
      filteredTweets: [],
      bar: {
        title: {
          text: 'Number of Tweets per Category',
          left: 'center',
          right: 'center'
        },
        grid: {
          top: '25%',
          bottom: '15%',
          left: '5%',
          right: '5%',
          containLabel: true
        },
        xAxis: {
          data: ['problem report', 'inquiry', 'irrelevant']
        },
        yAxis: {
          type: 'value'
        },
        series: [{
          name: 'Sales',
          type: 'bar',
          data: ['problem report', 'inquiry', 'irrelevant'],
          itemStyle: ITEM_STYLE_BAR_BLUE,
        }]
      }
    }),
    methods: {
      loadChartData(tweets) {
        if (tweets ) {
          let dataProblemReports = 0;
          let dataInquiries = 0;
          let dataIrrelevant = 0;

          tweets.forEach(function (tweet) {
            let val = tweet.tweet_class;
            if (val === 'problem_report') {
              dataProblemReports++;
            } else if (val === 'inquiry') {
              dataInquiries++;
            } else if (val === 'irrelevant') {
              dataIrrelevant++;
            }
          });
          this.bar.series[0].data = [dataProblemReports, dataInquiries, dataIrrelevant];
        }
      }
    },
    mounted() {
      this.loadChartData(this.$store.state.filteredTweets)
    },
    computed: {
      dataUpToDate() {
        if(this.$store.state.dataUpToDate) {
          this.loadChartData(this.$store.state.filteredTweets)
        }
        return this.$store.state.dataUpToDate;
      }
    }
  };
</script>

<style lang="scss" scoped>
.echarts {
  min-height: 250px;
  height: 100%;
  width: 100%;
  .chart {
    height: 100%;
    width: 100%;
  }
}
</style>
