export const BASE_URL = 'https://api.openreq.eu';
export const BACKUP_URL = 'https://appmate.informatik.uni-hamburg.de';

export const GET_ALL_TWEETS_ENDPOINT = function (twitterAccount) {
  return `${BASE_URL}/ri-storage-twitter/account_name/${twitterAccount}/all`;
};
export const GET_ALL_UNLABELED_TWEETS_ENDPOINT = function (twitterAccount) {
  return `${BASE_URL}/ri-storage-twitter/account_name/${twitterAccount}/all/unlabeled`;
};
export const POST_TWEET_LABEL_ENDPOINT = function () {
  return `${BASE_URL}/ri-storage-twitter/label/tweet/`;
};
export const POST_TWEET_LABEL_PAYLOAD = function (statusID, label) {
  let payload = {
    status_id: statusID,
    label
  };
  return JSON.stringify(payload);
};
export const POST_TWEET_LABEL_BACKUP_ENDPOINT = function () {
  return `${BACKUP_URL}/windtretrial/labeledtweet`;
};
export const POST_TWEET_LABEL_BACKUP_PAYLOAD = function (statusID, label, text) {
  let payload = {
    status_id: statusID,
    label,
    text
  };
  return JSON.stringify(payload);
};