<template>
  <v-container>
    <v-layout row>
      <filter-tool-bar/>
    </v-layout>
    <v-card flat class="header">
      <v-card-title>
        <h1>Problem Reports</h1> 
        <v-spacer/>
        <v-text-field
        v-model="searchProblemReports"
        append-icon="search"
        label="search"
        single-line
        hide-details
      ></v-text-field>
      </v-card-title>
    </v-card>
    <v-data-table
        :headers="tableHeaders"
        :items="problemReports"
        :search="searchProblemReports"
        v-if="dataUpToDate"
      >
        <template slot="items" slot-scope="props">
          <tr>
            <td style="text-align:'center'">
              {{ getFormattedDate(props.item.created_at) }}
            </td>
            <td class="issue-title-value">{{ props.item.text }}</td>
          </tr>
        </template>
      </v-data-table>

    <v-card flat class="header">
      <v-card-title class="justify-center">
        <h1>Inquiries</h1> 
        <v-spacer/>
        <v-text-field
        v-model="searchInquieries"
        append-icon="search"
        label="search"
        single-line
        hide-details
      ></v-text-field>
      </v-card-title>
    </v-card>
    <v-data-table
        :headers="tableHeaders"
        :items="inquiries"
        :search="searchInquieries"
        v-if="dataUpToDate"
      >
        <template slot="items" slot-scope="props">
          <tr>
            <td style="text-align:'center'">
              {{ getFormattedDate(props.item.created_at) }}
            </td>
            <td class="issue-title-value">{{ props.item.text }}</td>
          </tr>
        </template>
      </v-data-table>

    <v-card flat class="header">
      <v-card-title class="justify-center">
        <h1>Irrelevants</h1> 
        <v-spacer/>
        <v-text-field
        v-model="searchIrrelevants"
        append-icon="search"
        label="search"
        single-line
        hide-details
      ></v-text-field>
      </v-card-title>
    </v-card>
    <v-data-table
        :headers="tableHeaders"
        :items="irrelevants"
        :search="searchIrrelevants"
        v-if="dataUpToDate"
      >
        <template slot="items" slot-scope="props">
          <tr>
            <td style="text-align:'center'">
              {{ getFormattedDate(props.item.created_at) }}
            </td>
            <td class="issue-title-value">{{ props.item.text }}</td>
          </tr>
        </template>
      </v-data-table>
  </v-container>
</template>

<script>
  import axios from 'axios';
  import moment from 'moment';
  import 'moment/locale/de'
  import { POST_TWEET_LABEL_ENDPOINT, POST_TWEET_LABEL_PAYLOAD } from'./../../RESTconf.js'
  import {ACTION_SET_TOOLBAR_HEADER} from './../../store/types.js'
  import FilterToolBar from '../toolbar/FilterToolBar'
  export default {
    components: {
      FilterToolBar
    },
    data() {
      return {
        tableHeaders: [
          {text: 'Date', align: 'center', sortable: true, value: 'created_at', width: '10%'},
          {text: 'Tweet', align: 'left', sortable: true, value: 'text', width: '90%'},
        ],
        searchProblemReports: '',
        searchInquieries: '',
        searchIrrelevants: '',
        search: '',
        erros: [],
        problemReports: [],
        inquiries: [],
        irrelevants: []
      }
    },
    methods: {
      separateTweets(tweets) {
        //Sorted by creation date
        tweets = tweets.sort((val1, val2) => {
          return val1.created_at - val2.created_at
        });
        this.problemReports = []
        this.inquiries = []
        this.irrelevants = []

        tweets.forEach(tweet => {
          if (tweet.tweet_class === 'problem_report') {
            this.problemReports.push(tweet)
          } else if (tweet.tweet_class === 'inquiry') {
            this.inquiries.push(tweet)
          } else if (tweet.tweet_class === 'irrelevant') {
            this.irrelevants.push(tweet)
          }
        });
        
        // Update UI
        this.problemReports.splice(0, 0);
        this.inquiries.splice(0, 0);
        this.irrelevants.splice(0, 0);
      },
      getFormattedDate(date) {
        return moment(date, 'YYYYMMDD').format('DD.MM.YYYY')
      },
      removeTweetFromList(origin, index) {
        if (origin === "problem_report") {
          this.problemReports.splice(index, 1);
        } else if (origin === "inquiry") {
          this.inquiries.splice(index, 1);
        } else if (origin === "irrelevant") {
          this.irrelevants.splice(index, 1);
        }
      }
    },
    mounted() {
      this.$store.dispatch(ACTION_SET_TOOLBAR_HEADER, 'Tweet Overview');
    },
    computed: {
      dataUpToDate() {
        if(this.$store.state.dataUpToDate) {
          this.separateTweets(this.$store.state.filteredTweets);
        }
        return this.$store.state.dataUpToDate;
      }
    }
  }
</script>

